body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #282c34;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(8px + 2vmin);
  color: white;
}
header {
  color: #282c34;
  background-color: white;
  font-size: calc(10px + 2vmin);
  width: 100%;
  height: 8vh;
  min-height: 35px;
  padding: 5px 10px;
  font-weight: 600;
  box-sizing: border-box;
}
p {
  margin: 1vh 0;
}
hr {
  width: 86%;
}
button {
  margin:0 1%;
  border-radius: 3px;
  border:0;
  background-color: white;
  color: #282c34;
  cursor: pointer;
  font-weight: 600;
  padding:5px 15px;
}
.recorder-wrap {
  display: flex;
  height: 55vh;
  width: 100%;
  justify-content: center;
}
.recorder-wrap div {
  margin: 0 5%;
}
.recorder-wrap video {
  height:36vh;
  width:48vh;
}
.list-wrap {
  display: flex;
  flex-wrap: wrap;
  height: 30vh;
  width: 100%;
  overflow: auto;
  justify-content: center;
}
.thumbnail {
  margin: 1%;
}
.thumbnail video {
  width: 24vh;
  height: 20vh;
}
.thumbnail button {
  display: block;
  margin: auto;
}
.recorder-wrap {
  display: flex;
  height: 55vh;
  width: 100%;
  justify-content: center;
}
.recorder-wrap div.live {
  margin-right:5%;
}
.recorder-wrap div.recorded {
  margin-left:5%;
}
.recorder-wrap video {
  height:36vh;
  width:48vh;
}
.record-btn-wrap {
  display: flex;
  justify-content: center;
}
button[disabled] {
  color:lightgray;
  cursor: not-allowed;
}
